@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  src: url('./_assets/fonts/montserrat-regular.woff') format('woff'), 
       url('./_assets/fonts/montserrat-regular.woff2') format('woff2'); 
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 300;
  src: url('./_assets/fonts/montserrat-light.woff') format('woff'), 
       url('./_assets/fonts/montserrat-light.woff2') format('woff2');
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: bold;
  src: url('./_assets/fonts/montserrat-bold.woff') format('woff'), 
       url('./_assets/fonts/montserrat-bold.woff2') format('woff2');
}

body {
  margin:0;
  padding: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 h2 h3 h4{
  margin: 0%;
  padding: 0.25em;
}
